/*wrapper*/
.tabs-wrapper {}
.tabs-wrapper:after {    display: block; content: " "; height: 1px; clear: both;}

/*tab buttons*/
.tabs-wrapper .tabs  { overflow: hidden;    }
.tabs-wrapper .tabs .tab-link { cursor: pointer; background: none;     display: inline-block;  padding: 5px ; margin: 0; margin-right: $global-margin;  border-bottom: 2px solid;border-color: transparent;   border-bottom-left-radius: 0px;  border-bottom-right-radius: 0;}
.tabs-wrapper .tabs .tab-link.active { border-color: $active-color; color: $active-color; font-weight: bold; }

/*tab contents*/
.tabs-wrapper .tab-contents {   padding: $global-padding 0;  display: block;      border-radius: $border-radius; border-top-left-radius: 0; }


/*tabs top*/
// .tabs-wrapper.top .tab-contents { border-top: none; }
/*tabs bottom*/
.tabs-wrapper.bottom .tab-contents { border-bottom: none; }

/*tabs left*/
.tabs-wrapper.left { display: flex; }
.tabs-wrapper.left .tabs { display: block;   float: left; width: 300px; }
.tabs-wrapper.left .tabs .tab-link { float:  none; display: block; width: 100% }
.tabs-wrapper.left .tab-contents { display: inline-block; position: relative; left: 0;  width: calc(100% - 300px); border: 1px solid #ccc; border-left: none; }



/*tabs right*/
.tabs-wrapper.right { display: flex; }
.tabs-wrapper.right .tabs { display: block;   float: left; width: 300px; }
.tabs-wrapper.right .tabs .tab-link { float:  none; display: block; width: 100% }
.tabs-wrapper.right .tab-contents { display: inline-block; position: relative; left: 0;  width: calc(100% - 300px); border: 1px solid #ccc; border-right: none; }



  
@media (max-width: $tablet) {
    .tabs-wrapper.right,.tabs-wrapper.left  { flex-direction: column; }
    .tabs-wrapper.left .tabs, .tabs-wrapper.right .tabs { display: block;  width: 100%; }
    .tabs-wrapper.right .tab-contents,.tabs-wrapper.left .tab-contents  { display: block; width: 100%;}
}