select::-ms-expand { display: none; }
/*password visible toggle*/
button.toggle-visibility { border: none; position: absolute; top: 0; right: 0; height: 33px; background: transparent !important; color: $black !important; }
.input-wrapper.error button.toggle-visibility,
.input-wrapper.valid button.toggle-visibility { padding-right: 30px; }
/*password strength*/
.password-strength-container { overflow: auto; margin-top: 10px; margin-bottom: 10px; }
.password-strength-container .strength { font-size: 0.9em; text-transform: capitalize; }
.password-strength-container .meter { height: 10px; margin-top: 5px; position: relative; background: $global-border; border-radius: $border-radius; }
.password-strength-container .meter > .password-bar { display: block; height: 100%; border-radius: $border-radius; position: relative; overflow: hidden; }
.password-strength-container .meter > .strong { background-color: $green; border-radius: $border-radius; @extend .smooth-transition; }
.password-strength-container .meter > .medium { background-color: $yellow; @extend .smooth-transition; }
.password-strength-container .meter > .weak { background-color: $red; @extend .smooth-transition; }

.required-indicator { color: $error-color; }
 
.input-wrapper{

  .switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 20px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  .switch-title {
    margin-right: auto;
    margin-left: 10px;
    display: inline-block;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 1px;
    bottom: 1px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    box-shadow: 2px 2px 5px #c8c7c7;
  }

  input:checked + .slider {
    background-color: $active-color;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(25px);
    -ms-transform: translateX(25px);
    transform: translateX(25px);
    box-shadow: unset;
  }
    /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
    border: 1px solid #c8c7c7;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .-switch-indicator {
    position: absolute;
    height: 10px;
    top: 5px;
    left: 10px;
    cursor: pointer;  
  }

.switch-input .switch-container {
  display: flex;
  position: relative;
}
.switch-disabled {
  .slider {
    background-color: var(--gray100) !important; //TODO - remove !important when duplicate styles are removed
  }
}

.switch-inline {
  .switch-container {
    display: inline-block;
  }
  .switch-title {
    margin-right: auto;
    margin-left: 10px;
    display: inline-block;
  }
}

