
  .tag-button {
    @extend .btn;
    @extend .small;
    background: $light-grey;
    @include themes($themes) {
      background: $light-grey;
    }
    color: $font-color;
    margin-right: $global-margin
  }
  .tag-button.btn {
    background: $light-grey;
    @include themes($themes) {
      background: $light-grey;
    }
  }

  .tag-button button {
    margin: $global-padding * -1 $global-padding * -1 $global-padding * -1 $global-padding/2 !important;
    padding:  $global-padding/4 $global-padding/2 !important;
    border-radius: 0 $border-radius $border-radius 0;
    outline: none !important;
  }
  button.tag-button:hover,
  input[type="button"].tag-button:hover,
  a.tag-button:hover,
  button.tag-button:focus,
  input[type="button"].tag-button:focus,
  a.tag-button:focus {
    button {
      @extend .btn.active;
    }
  }

  .pill {
    @extend .tag-button;
    min-width: 75px;
    display: inline-block;
    color: white;
    font-weight: 600;
    padding: 0px 10px;
    text-align: center;
    margin: 0;
    border-radius: 10px;
  }
