.message {  position: relative; margin-bottom: $global-margin * 2;   border-radius: $border-radius; color: #FFF; border-color: $info-color; overflow: hidden; }
.message .icon { color: #FFF; display: block; height: 100%; width: $global-line-height * 2; line-height: 62px; text-align: center; position: absolute; top: 0; left: 0; font-size: 20px; border-radius: $border-radius;  }
.message .close { position: absolute; top: 1em; right: 1em; cursor: pointer; }
.message .copy { padding: 20px 40px 20px 44px; }
.message .copy p { margin: 0; }
.message-info, 
 .message-loading { background-color:  rgba($info-color, .8);  border-color: $info-color; }
.message-warning {background-color:  rgba($warning-color, .8); border-color: $warning-color; }
 
.message-error { background-color:  rgba($error-color, .8);  border-color: $error-color; }
 
.message-success {background-color:  rgba($success-color, .8);   border-color: $success-color; }
 
