.global-font { @include themes($themes) { font-family: themed("global-font-family"); } font-size: $global-font-size; line-height: $global-line-height; font-weight: 400; }
.headers { @include themes($themes) { font-family: themed("special-font-family"); } margin: 0 0 $global-margin * 2 0; }
@include themes($themes) { font-family: themed("global-font-family"); }
h1, .h1 { @extend .headers; font-size: 44px; line-height: 52px; }
h2, .h2 { @extend .headers; font-size: 36px; line-height: 48px; }
h3, .h3 { @extend .headers; font-size: 28px; line-height: 40px; }
h4, .h4 { @extend .headers; font-size: 22px; line-height: 30px; }
h5, .h5 { @extend .headers; font-size: 18px; line-height: 28px; }
h6, .h6 { @extend .headers; font-size: 16px; line-height: 24px; }
p, .p { @extend .global-font; margin: 0 0 $global-margin * 2 0; }
small, .small { font-size: $small-font-size; line-height: $global-line-height; }
a { color: $link-color; text-decoration: none; @extend .smooth-transition-color; }
a:hover { text-decoration: underline; color: $link-color; }
a:focus, a.active { text-decoration: underline; color: $link-color; }
label { font-size: $global-font-size; line-height: $global-line-height; font-weight: 600;  }
.title { padding-bottom: $global-padding; border-bottom: $global-border; }
