/********** START ACCORDION **********/
.accordions-wrapper { margin-bottom: 0px; }
.accordion { margin-bottom: 10px; }
.accordion .accordion-heading { background-color: $grey; cursor: pointer; display: block; padding: 10px; padding-right: 30px; position: relative; border-radius: $border-radius; }
.accordion .accordion-heading:hover { background: $default-primary-color; color: #fff; @extend .smooth-transition; }
.accordion .accordion-content { overflow: hidden; display: none; }
.accordion .accordion-content.open { display: block; }
.accordion .accordion-content .accordion-content-inner { padding: $global-padding; }
.accordion-indicator { position: absolute; right: 10px; top: 50%; height: 20px; margin-top: -10px; }
.accordion .accordion-heading .accordion-indicator:before {font-family: 'FontAwesome';   content: "\f107"; }
.accordion .accordion-heading.active .accordion-indicator:before { font-family: 'FontAwesome'; content: "\f106"; }
/********** END ACCORDION **********/
