.pagination { align-items: baseline; padding: 0; text-decoration: none; display: flex; }
.pagination .btn { margin: 0;  }
.pagination-pages,
.pagination-prev,
.pagination-first,
.pagination-next,
.pagination-last { border-radius: 0px; }
.pagination ul { display: flex; }
.pagination ul li:first-child .btn { border-radius: $border-radius/2 0 0 $border-radius/2; }
.pagination ul li:last-child .btn { border-radius: 0 $border-radius/2 $border-radius/2 0; }
.pagination .items-per-page { width: auto; display: block; margin-left: auto; margin-right: 0; height: 36px; }
.pagination .items-per-page-wrapper { align-items: stretch; display: flex; margin-left: auto; margin-right: 0; }
.pagination .items-per-page-wrapper .flex-item { position: relative; display: flex; flex-wrap: wrap; }
.pagination .items-per-page-wrapper .flex-item:first-child { border-radius: $border-radius 0 0 $border-radius; border-right: none; }
.pagination .items-per-page-wrapper .flex-item:last-child { border-radius: 0 $border-radius $border-radius 0; }
.pagination .input-default { border-top-left-radius: 0; border-bottom-left-radius: 0; height: $global-line-height + 14px; padding-right: $global-padding * 3; border: $light-border; }

@media (max-width: $tablet) {
  .pagination { display: block; }
  .pagination ul { text-align: center; width: 100%; }
}
