.dropdown-menu { position: absolute; top: 100%; left: 0; z-index: 1;  float: left; min-width: 120px; padding: 0; margin: 0; font-size: 1rem; color: #212529; text-align: left; list-style: none; background-color: #fff; background-clip: padding-box; border: none; box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.25); border-radius: 0; }
.dropdown-item { padding: 0; margin: 0; }
.dropdown-item:active { background-color: $light-grey; }
.dropdown-item:last-child { border-bottom: none; margin: 0; }
.dropdown-item a,
.dropdown-item > span { padding: $global-padding; display: block; text-decoration: none; color: $font-color; font-size: $small-font-size; cursor: pointer; }
.dropdown-icon { margin-left: $global-margin; font-family: "Font Awesome 5 Free", sans-serif; font-weight: 900; -webkit-font-smoothing: antialiased; display: inline-block; font-style: normal; font-feature-settings: normal; font-variant: normal; text-rendering: auto; line-height: 1; }
.dropdown-icon::before { content: "\f106"; }
.dropdown-wrapper { position: relative; }
.dropdown { position: absolute; top: 100%; right: 0; display: none; padding-top: 10px; margin-top: -1px; z-index: 2; }
.dropdown:before { content: ""; position: absolute; bottom: calc(100% - 10px); right: 0; margin-right: 10px; width: 0; height: 0; border: 10px solid transparent; border-bottom-color: $default-primary-color; z-index: 1; }
.dropdown:after { content: ""; position: absolute; bottom: calc(100% - 10px); right: 0; margin-right: 11px; width: 0; height: 0; border: 9px solid transparent; border-bottom-color: $default-primary-color; z-index: 2; }
.dropdown-wrapper .js-expand-dropdown:focus + .dropdown,
.dropdown-wrapper:hover .dropdown { display: block; }
.dropdown li { display: block !important; margin: 0; }
.dropdown .btn { display: block; margin: 0;  width: 100% }
