.editor-container { height: auto; padding: 0; border: none; }
.editor-container .editor-content { background-color: $white; @include themes($themes) { background-color: $white; }  min-height: 300px; padding: $global-padding; border-bottom-left-radius: $border-radius; border-bottom-right-radius: $border-radius; border: $global-border; border-top: none; }
.editor-container .editor-toolbar { background-color: $default-primary-color; @include themes($themes) { background-color: themed("primary-color"); } border-top-left-radius: $border-radius; border-top-right-radius: $border-radius; }
.editor-container .editor-commands > button { margin: 0; border-radius: 0%; }
.editor-container .editor-dropdown-container { display: inline-block; }
.editor-container .editor-dropdown-container .editor-dropdown { position: absolute; display: none; margin: 0 1px; }
.editor-container .editor-dropdown-container:hover .editor-dropdown { display: block; background: $light-grey; @include themes($themes) { background: $light-grey; } padding: $global-padding; }
.editor-container .editor-container .editor-btn-tooltip > .tooltip-item > button { margin: 0; border-radius: 0%; }
.editor-container .td-table-picker { margin: 0; padding: 0; height: 10px; width: 10px !important; border: $global-border !important; }
.editor-container .td-table-picker-selected { @include themes($themes) { background-color: themed("active-color"); } }
.editor-container .btn-table-picker { margin: 0; padding: 8px; background-color: transparent; }
.editor-container .btn-headering { padding: $global-padding; background: transparent; @include themes($themes) { color: themed("primary-color"); border: 1px solid themed("primary-color"); }  margin-bottom: 3px; }
.editor-container .btn-headering:hover { @include themes($themes) { background: themed("primary-color"); } color: $white; @include themes($themes) { color: $white; } }
 