body { margin: 0; font-family: $global-font-family; color: #333; font-size: $global-font-size; line-height: $global-line-height }
img { max-width: 100%; }
table { border-collapse: collapse; border-spacing: 0; width: 100%; border: $global-border; }
table td,
table th { border: $global-border; padding: 5px; }
pre { margin-bottom: 20px; }
small,
.small { font-size: 10px; }
ol,
ul { margin: 0 0 $global-margin * 2 $global-margin * 2; padding: 0; }
li { margin-bottom: $global-margin/2; }
img { max-width: 100%; max-height: 100%; }
pre { margin-bottom: $global-margin * 2; }
hr { border-bottom: 1px solid $grey; }
strong { font-weight: bold; }
