.card {     box-shadow: 3px 4px 20px 0px #2e3d4926;
    border-radius: 4px; position: relative; display: flex; flex-direction: column; min-width: 0; word-wrap: break-word; background-color: $white;  background-clip: border-box; border: $light-border; @include themes($themes) { background-color: $white; } }
.card-header { padding: 0.75rem 1.25rem; margin-bottom: 0; border-bottom: $light-border; }
.card-body { flex: 1 1 auto; padding: 1.25rem; }
.card-footer { padding: 0.75rem 1.25rem;  border-top: $light-border; }

.landing-button  .card {margin: 20px; }
.landing-button:nth-child(5n)  .card {color: #BB7824; background-color: #bb78241a !important; border-radius: 5px; }
.landing-button:nth-child(5n+1)  .card { color:#3369e8; background-color: #3369e81a  !important; border-radius: 5px;}
.landing-button:nth-child(5n+2)  .card { color: #16A085 ; background-color:#16a0851a !important; border-radius: 5px;}
.landing-button:nth-child(5n+3)  .card { color:#d50f25 ; background-color: #d50f251a!important; border-radius: 5px;}
.landing-button:nth-child(5n+4)  .card { color:#8c0fd5 ; background-color: #7c0fd51a!important; border-radius: 5px;}
