/*******TABS START******/
ul.tabs-menu { margin-left: 0; padding-left: 0; }
ul.tabs-menu:after { content: " "; @extend .clearfix; }
.tabs-menu li { display: inline-block; /*margin-right: 10px;*/ margin-bottom: -1px; background-color: $grey; border-top: $global-border; border-right: $global-border; border-left: $global-border; }
.tabs-menu li { position: relative; z-index: 1; }
.tabs-menu li a { padding: 10px 20px; display: block; border-bottom: $global-border; text-decoration: none !important; }
.tabs-menu li a.active { background-color: #fff; border-bottom: 1px solid transparent; }
.tabs-menu .active a { color: $default-primary-color; }
.tab { border: $global-border; background-color: #fff; width: auto; clear: both; }
.tab-content.js-tab-content { display: none; }
.tab-content.js-tab-content.active { display: block; }
/*******TABS END******/
