.calendar { width: 100%; border-radius: $border-radius; border: $global-border; overflow: hidden; background: $light-grey;    background: #fff; padding: 5px; margin-bottom: $global-margin; @include themes($themes) { background: $light-grey; } }
.calendar .btn { margin: 0; margin-right: 0; border-radius: 0; }
.calendar .tile { background-color: $light-grey; color: $font-color; @include themes($themes) { background-color: $light-grey; } }
.calendar .tile:hover,
.calendar .tile:focus,
.calendar .tile.active { @extend .btn; }
.calendar .month-view-weekdays abbr { text-decoration: none; }
.calendar .navigation { display: flex; flex-wrap: wrap; }
.calendar .navigation .month-title { flex-grow: 1; }
.calendar .month-view-weekdays { display: flex; flex-wrap: wrap; }
.calendar .month-view-weekdays .weekday { flex-basis: 14.2857%; max-width: 14.2857%; overflow: hidden; text-align: center; padding: $global-padding; }
.month-view-days { display: flex; flex-wrap: wrap; }
.calendar .month-view-days .tile { border-color: transparent !important; flex-basis: 14.2857%; max-width: 14.2857%; overflow: hidden; text-align: center; padding: $global-padding; }
.calendar .day.other-month { opacity: 0.5; }
.calendar .day.today { border-color: $default-primary-color !important; @include themes($themes) { border-color: themed("primary-color") !important; } }
.calendar .day.today:hover,
.calendar .day.today:focus,
.calendar .day.today.active { @extend .btn; }
.calendar .day.btn { margin-right: 0 !important; border-radius: 0; }
.input-date-calendar-wrapper { max-width: 350px; position: fixed; top: 100%; left: 0; z-index: 3; }

