.notifcations-container {     position: fixed;
    position: fixed;
    bottom: 0;
    margin: 10px;
    left: 50%;
    width: 100%;
    max-width: 500px;
    z-index: 3;
    transform: translateX(-50%);}
.notifications-item {  @extend .smooth-transition; }
.notifications-close { position: absolute; top: 25px; right:  $global-margin* 2; cursor: pointer; }

.notifications-item-shown { max-height: 100vh; }
.notifications-item-hidden { max-height: 0em; }
