/*bg colors*/
.bg-primary { background-color: $default-primary-color !important; @include themes($themes) { background-color: themed("primary-color") !important; } }
.bg-active { background-color: $active-color !important; @include themes($themes) { background-color: themed("active-color") !important; } }
.bg-error { background-color: $error-color !important; }
.bg-success { background-color: $success-color !important; }
.bg-white { background-color: #fff !important; }
.bg-warning { background-color: $warning-color !important; }
.bg-info { background-color: $blue !important; }
.bg-check { background-color: $green !important; }
.bg-light-grey { background-color: $light-grey !important; @include themes($themes) { background-color: $light-grey !important; } }
.bg-input-grey { background-color: $input-background !important; }
.bg-grey { background-color: $grey !important; }
.bg-dark-grey { background-color: $dark-grey !important; }
.bg-black { background-color: $black !important; }
.bg-transparent { background-color: transparent !important; }

.bg-blue { background-color: $blue !important; }
.bg-red { background-color: $red !important; }
.bg-yellow { background-color: $yellow !important; }
.bg-green { background-color: $green !important; }

 

/*font colors*/
.color-font { color: $font-color !important; }
.color-primary { color: $default-primary-color !important; @include themes($themes) { color: themed("primary-color") !important; } }
.color-active { color: $default-primary-color !important; @include themes($themes) { color: themed("active-color"); } }
.color-error { color: $error-color; }
.color-success { color: $success-color; }
.color-warning { color: $warning-color; }
.color-grey { color: $light-grey !important; @include themes($themes) { color: $light-grey !important; } }
.color-white { color: #fff !important; }

/*font weight*/
.bold { font-weight: bold; }

/*float*/
.fl { float: left !important; }
.fr { float: right !important; }
.clearfix { display: block; height: 1px; zoom: 1; width: 100%; clear: both; margin-bottom: -1px; }

/*text align*/
.tl { text-align: left !important; }
.tr {   text-align: right !important; }
.tc { text-align: center !important; }

/*list*/
.list-inline { margin: 0; padding-left: 0; list-style: none; }
.list-inline li { display: inline-block; margin: 0; }
.no-list { list-style: none; margin-left: 0; }

/*narrow content*/
.narrow-content { max-width: $tablet; margin: 0 auto; }

/*visibility*/
.hidden { display: none; }
.visible { display: block; }

/*effects*/
.shadow { box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important; }

/*radios*/
.radius { border-radius: 10px 10px 10px 10px; }
.no-border { border: none !important; }

/*text*/
.ellipsis { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; -o-text-overflow: ellipsis; -ms-text-overflow: ellipsis; display: inline-block; max-width: 100%; }
.uppercase { text-transform: uppercase; }

/*position*/
.relative { position: relative !important; }
.absolute { position: absolute !important; }
.inner { margin: $global-margin/2; padding: $global-padding/2; }

/*width*/
.auto-width { width: auto !important; }
.full-width { width: 100%; max-width: none; }

/*overflow*/
.overflow-hidden { overflow: hidden; }

/*display*/
.display-inlineblock { display: inline-block; }
.display-block { display: block; }
.display-inline { display: inline; }
.display-flex { display: flex; }

/*cursor*/
.cursor-pointer { cursor: pointer; }
@media (min-width: $desktop) {
  .desktop-flex { display: flex !important; }
}
@media (min-width: $tablet) {
  .laptop-hidden { display: none !important; }
}
@media (max-width: $tablet) {
  .tablet-hidden { display: none !important; }
  .tablet-displayed { display: block !important; }
}
@media (max-width: $mobile) {
  .mobile-hidden { display: none !important; }
  .mobile-displayed { display: block !important; }
}

/*margins*/
.m0 { margin: 0 !important; }
.mt0 { margin-top: 0 !important; }
.mr0 { margin-right: 0 !important; }
.mb0 { margin-bottom: 0 !important; }
.ml0 { margin-left: 0 !important; }
.m10 { margin: 10px !important; }
.mt10 { margin-top: 10px !important; }
.mt20 { margin-top: 20px !important; }
.mt30 { margin-top: 30px !important; }
.mt40 { margin-top: 40px !important; }

.mr10 { margin-right: 10px !important; }
.mb10 { margin-bottom: 10px !important; }
.ml10 { margin-left: 10px !important; }
.mb20 { margin-bottom: 20px !important; }
.mb40 { margin-bottom: 40px !important; }

/*paddings*/
.p0 { padding: 0 !important; }
.p10 { padding: 10px !important; }
.p20 { padding: 20px !important; }
.pt0 { padding-top: 0 !important; }
.pr0 { padding-right: 0 !important; }
.pb0 { padding-bottom: 0 !important; }
.pl0 { padding-left: 0 !important; }
